import request from '@/utils/request'

/**
 * 文章提交接口
 * @param {*} data
 *
 */
export function essaySubmitAPI(data) {
    return request({
        url: `feedback`,
        method: 'post',
        data: data,
        headers: {
            'api-key': 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9',
            // 'Content-Type': 'text/plain',
            // 'Cookie': 'JSESSIONID=66FFC47AFD42865E4DD59CAE020C2968',
        },
        // body: data.body
    })
}


